<script>
import { mapActions, mapState } from 'vuex';

export default {
  name: "HeaderComponent",
  data() {
    return {
      logoUrl: '/logo-unionsearch.jpg'  // 로고 이미지 경로 설정
    };
  },
  computed: {
    ...mapState(['isLoggedIn', 'userName'])  // Vuex 상태에서 로그인 상태와 사용자 이름을 가져옴
  },
  created() {
    this.checkLoginStatus();  // 컴포넌트가 생성될 때 로그인 상태를 확인
  },
  methods: {
    ...mapActions(['checkLoginStatus', 'logout']),  // Vuex 액션 사용

    // 로그아웃 처리
    async handleLogout() {
      try {
        const result = await this.logout();  // 로그아웃 처리 (Vuex 스토어에 정의된 로그아웃 액션 호출)
        if (result.success) {
          this.$router.push('/');  // 로그아웃 성공 시 홈으로 이동
        } else {
          console.error('로그아웃에 실패했습니다:', result.message);
        }
      } catch (error) {
        console.error('로그아웃 처리 중 오류:', error);
      }
    }
  }
};
</script>

<template>
  <nav class="navbar navbar-expand-lg navbar-dark" style="background-color: #092a49;">

    <router-link to="/" class="navbar-brand company-name">
      유니온써치
      <img :src="logoUrl" alt="Company Logo" class="company-logo" />
    </router-link>

    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarCollapse"
      aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <!-- 수정 내용 -->
    <div class="collapse navbar-collapse" id="navbarCollapse">
      <div class="navbar-nav ms-auto nav-right-items">
        <!-- 회사소개 Dropdown -->
        <div class="nav-item dropdown">
          <a class="nav-link dropdown-toggle" href="#" id="companyDropdown" role="button" data-bs-toggle="dropdown"
            aria-expanded="false">
            회사 소개
          </a>

          <ul class="dropdown-menu" aria-labelledby="companyDropdown">
            <li><router-link to="/ceo-greeting" class="dropdown-item">대표이사 인사말</router-link></li>
            <li><router-link to="/organization-chart" class="dropdown-item">조직도</router-link></li>
            <li><router-link to="/history" class="dropdown-item">연혁</router-link></li>
            <li><router-link to="/location" class="dropdown-item">오시는 길</router-link></li>
            <li><router-link to="/contact" class="dropdown-item">연락처</router-link></li>
          </ul>
        </div>

        <router-link to="/consultant" class="nav-item nav-link">컨설턴트</router-link>
        <router-link to="/open-position" class="nav-item nav-link">오픈 포지션</router-link>
        <router-link to="/talent-database" class="nav-item nav-link">인재 DB</router-link>
        <router-link to="/seminar-archive" class="nav-item nav-link">기술세미나 자료실</router-link>

        <!-- 로그인 여부에 따른 동적 렌더링 -->
        <div v-if="isLoggedIn" class="nav-item dropdown">
          <a class="nav-link dropdown-toggle" href="#" id="userDropdown" role="button" data-bs-toggle="dropdown"
            aria-expanded="false">
            {{ userName }} <!-- 로그인된 사용자의 이름 표시 -->
          </a>

          <ul class="dropdown-menu" aria-labelledby="userDropdown">
            <li>
              <button @click="handleLogout" class="dropdown-item">로그아웃</button> <!-- 로그아웃 버튼 -->
            </li>
          </ul>

        </div>
        <router-link v-else to="/login" class="nav-item nav-link">로그인</router-link> <!-- 비로그인 시 로그인 버튼 -->
      </div>
    </div>
  </nav>
</template>

<style scoped>
.company-logo {
  height: 50px;
}
</style>
